import React from "react";
import cx from "classnames";
import css from "./VideoRelated.module.scss"
import {Video, MessageEnd} from "../index";

export default (props) => {
    let relatedVids = props.relatedVids
    let videos;

    if (relatedVids) {
        videos = relatedVids.map((video) => {
            let vid = video.snippet
            let id = video.id
            return (
                <Video
                    className={css.video}
                    key={id.videoId}
                    videoId={id.videoId}
                    title={vid.title}
                    videoInfoContainerCss={css.videoInfoContainer}
                    thumbnailWidth={'60%'}
                    thumbnailHeight={'100px'}
                    thumbnailImg={vid.thumbnails.medium.url}
                    channelTitle={vid.channelTitle}
                />
            )
        })
    }

    return (
        <div className={cx(css.relatedVids, props.contentStyle)}>
            <p>Related videos</p>
            { relatedVids
                ?
                <div className={css.container}>
                    { videos }
                </div>
                :
                <MessageEnd message={'Error loading :/'} />
            }
        </div>
    )
}