import React, { useState, useEffect} from "react";
import { withRouter } from 'react-router';
import {youtube, youtubeParams} from "../../api/youtube";
import { VideoPlaying, VideoDetail, VideoSearch, VideoRelated } from "../../components";
import css from './VideoPage.module.scss'

function VideoPage(props) {
    const [playingVid, setPlayingVid] = useState([])
    const [relatedVids, setRelatedVids] = useState([])

    const videoID = props.location.search.slice(3)
    const videoSrc = `https://www.youtube.com/embed/${videoID}?autoplay=1`;

    useEffect(() => {
        const videoId = async () => {
            const vidData = await youtube.get('search', {
                params: {
                    ...youtubeParams,
                    q: videoID
                }
            })
            const relatedVidsData = await youtube.get('search', {
                params: {
                    ...youtubeParams,
                    type: 'video',
                    relatedToVideoId: videoID
                }
            })
            setRelatedVids(relatedVidsData.data.items)
            setPlayingVid(vidData.data.items[0].snippet)
        }
        videoId()
            .catch(err => {
                console.log(err.response.data.error)
            })
    }, [videoID, videoSrc])

    return (
        <div className={css.screen}>
            <div className={css.pageContent}>
                <div className={css.videoSection}>
                    <VideoPlaying vidSrc={videoSrc} />
                    <VideoDetail
                        contentStyle={css.content}
                        playingVid={playingVid}
                        videoID={videoID}
                    />
                </div>
                <div className={css.sideContent}>
                    <VideoSearch contentStyle={css.content} />
                    <VideoRelated
                        relatedVids={relatedVids}
                        contentStyle={css.content}
                    />
                </div>
            </div>
        </div>
    )
}

export default withRouter(VideoPage)