import React, {useState} from "react";
import {MdKeyboardArrowDown} from "react-icons/md";
import {IoLogoYoutube} from "react-icons/io";
import cx from "classnames";
import css from "./VideoDetail.module.scss";

const btnStyle = {
    transform: 'rotateX(180deg)'
}

export default (props) => {
    const [expanded, setExpanded] = useState(false)
    const handleArrowClick = () => setExpanded(prev => !prev)
    let playingVid = props.playingVid
    let channelId = playingVid.channelId

    const viewOnYouTube = (
        <div className={css.viewOnYoutube}>
            <a
                href={`https://youtube.com/watch?v=${props.videoID}`}
                target={'_blank'}
                rel={"noopener noreferrer"}
            >
                <span>Click to view video stats and comments on Youtube</span>
                <IoLogoYoutube size={15} color={'#767676'} />
            </a>
        </div>
    )

    return (
        <div className={css.container}>
            { playingVid
                ?
                <div>
                    <div className={cx(css.videoTitleContainer, props.contentStyle)}>
                        <h3>{playingVid.title}</h3>
                        <button onClick={handleArrowClick}>
                            <MdKeyboardArrowDown
                                color={'#f2f2f2'}
                                size={20}
                                style={expanded ? null : btnStyle}
                            />
                        </button>
                    </div>
                    <div className={cx(css.creatorContainer, props.contentStyle)}>
                        <h3 className={css.creator}>Posted&nbsp;by:&nbsp;<span>{playingVid.channelTitle}</span></h3>
                        { channelId ?
                            <a
                                href={`https://m.youtube.com/channel/${channelId}`}
                                target={'_blank'}
                                rel={"noopener noreferrer"}
                                className={css.subscribe}
                            >
                                <IoLogoYoutube size={20} color={'#ff0000'} />
                                <span className={css.channel}>CHANNEL</span>
                            </a>
                            :
                            <div className={css.channel}>CHANNEL</div>
                        }
                    </div>
                    { expanded
                        ?
                        <div className={cx(css.description, props.contentStyle)}>
                            <p>{playingVid.description}</p>
                            { viewOnYouTube }
                        </div>
                        :
                        null
                    }
                </div>
                :
                <div className={cx(css.description, props.contentStyle)}>
                    { viewOnYouTube }
                </div>
            }
        </div>
    )
}