import React from "react";
import css from "./VideoPlaying.module.scss"

export default (props) => {
    return (
        <div className={css.container}>
            <iframe
                frameBorder="0"
                height="100%"
                width="100%"
                title="Video Player"
                allowFullScreen
                allow="autoplay"
                src={props.vidSrc}
            />
        </div>
    )
}