import React from "react";
import { path } from "../../pages";
import { Link } from "react-router-dom"
import Truncate from 'react-truncate';
import cx from 'classnames'
import useMediaQuery from "../../constants/useMediaQuery";

import css from './Video.module.scss'

function Video(props) {
    const tablet = useMediaQuery("(min-width: 768px)");
    return (
        <Link className={cx(css.video, props.className)} to={{
            pathname: path.watch,
            search: "v=" + props.videoId
        }}>
            <div className={css.thumbnail}>
                <img src={props.thumbnailImg} alt={props.title + ' thumbnail'}/>
                {/*<span className={css.videoLength}>4:65</span>*/}
            </div>
            <div className={props.videoInfoContainerCss}>
                <Truncate
                    className={css.vidTitle}
                    lines={2} ellipsis={<span>... </span>}
                >{props.title}</Truncate>
                <div>
                    <Truncate
                        className={css.infoText}
                        lines={1} ellipsis={<span>... </span>}
                    >{props.channelTitle}</Truncate>
                    <span className={css.dotSeparator}>•</span>
                    <span className={css.infoText}>2 weeks ago</span>
                    { tablet
                        ?
                        <div className={css.description}>
                            {props.description}
                        </div>
                        :
                        null
                    }
                </div>
            </div>
        </Link>
    )
}

export default Video