import React, {useState} from "react";
import cx from "classnames";
import css from "./VideoSearch.module.scss";
import {Link} from "react-router-dom";
import {path} from "../../pages";

export default (props) => {
    const [newVidID, setNewVidID] = useState('')
    const validator = RegExp('^[A-Za-z._-]')

    let button = (
        <div className={cx(css.button, css.invalidBtn)}
        >Watch</div>
    )

    if (validator.test(newVidID) && newVidID.length === 11) {
        button = (
            <Link className={cx(css.button, css.validBtn)} to={{
                pathname: path.watch,
                search: "v=" + newVidID
            }}>Watch</Link>
        )
    }

    return (
        <div className={cx(css.videoInput, props.contentStyle)}>
            <h3>Search a video by id</h3>
            <div>
                <input
                    type="text"
                    placeholder={'Video id'}
                    value={newVidID}
                    onChange={e => setNewVidID(e.target.value)}
                />
                { button }
            </div>
        </div>
    )
}