import React, { useState, useEffect } from "react";
import { withRouter } from 'react-router';
import {Content, Video, LoadingSpinner} from "../../components";
import ErrorPage from "../ErrorPage/ErrorPage";
import { youtube, youtubeParams } from "../../api/youtube";
import Truncate from 'react-truncate';

import css from './SearchPage.module.scss'

function SearchPage(props) {
    const [err, setErr] = useState(false)
    const [dataLoading, setDataLoading] = useState(true)
    const [searchVids, setSearchVids] = useState([])
    const actualSearch = props.location.search.slice(3)

    useEffect( () => {
        const fetchParams = async () => {
            const res = await youtube.get('search', {
                params: {
                    ...youtubeParams,
                    part: 'snippet',
                    q: actualSearch
                }
            })
            setDataLoading(false)
            setSearchVids(res.data.items)
        }
        fetchParams()
            .catch(err => {
                console.log(err.response.data.error)
                setErr(err.response.status)
            })
    }, [actualSearch])

    const videos = searchVids.map((video) => {
        let vid = video.snippet
        let id = video.id
        return (
            <Video
                className={css.video}
                key={id.videoId}
                videoId={id.videoId}
                title={vid.title}
                videoInfoContainerCss={css.videoInfoContainer}
                thumbnailImg={vid.thumbnails.medium.url}
                channelTitle={vid.channelTitle}
                description={vid.description}
            />
        )
    })

    if (err) return <ErrorPage not404={true} />
    return (
        <Content className={css.screen}>
            {dataLoading
                ?
                <LoadingSpinner />
                :
                <div className={css.content}>
                    { searchVids.length !== 0
                        ?
                        <div>
                            <div className={css.pageTitleContainer}>
                                <div className={css.pageTitle}>
                                    <span>Results&nbsp;for:&nbsp;</span>
                                    <Truncate
                                        lines={1} ellipsis={<span>... </span>}
                                    >"{actualSearch}"</Truncate>
                                </div>
                            </div>
                            <div className={css.videoMainList}>
                                { videos }
                            </div>
                        </div>
                        :
                        <div className={css.noResults}>
                            <h1>No result found :/</h1>
                        </div>
                    }

                </div>
            }
        </Content>
    )
}

export default withRouter(SearchPage)