import React, { useState } from "react";
import { IoIosSearch, IoLogoYoutube } from "react-icons/io";
import { Link } from "react-router-dom";
import { path } from '../../pages'
import useMediaQuery from "../../constants/useMediaQuery";

import css from './Header.module.scss'

function Header() {
    const tablet = useMediaQuery("(min-width: 768px)");
    const [query, setQuery] = useState('')

    const handleKeyPress = (target) => {
        if (target.charCode === 13) {
            alert('Click the search icon')
        }
    }

    return (
        <div className={css.header}>
            <Link to={path.home} className={css.logo}>
                <img src={`${path.home}/assets/logo32.png`} alt="YTC Logo"/>
                { tablet ? <span className={css.ytname}>YT CLONE</span> : null }
            </Link>
            <div className={css.searchBar}>
                <input
                    placeholder={'Search'}
                    value={query}
                    onChange={e => setQuery(e.target.value)}
                    onKeyPress={handleKeyPress}
                />
                <Link to={{
                    pathname: path.search,
                    search: "q=" + query
                }}>
                    <IoIosSearch />
                </Link>
            </div>
            { tablet
                ?
                <a
                    target={"_blank"}
                    href={'https://www.youtube.com/'}
                    rel={"noopener noreferrer"}
                >
                    <IoLogoYoutube size={20} color={"grey"} />
                </a>
                :
                null
            }

        </div>
    )
}

export default Header