import React from "react";
import css from './ErrorPage.module.scss'
import { VideoSearch } from "../../components";

const reloadPage = () => {
    window.location.reload();
}

export default ({not404}) => (
    <div className={css.screen}>
        { not404
            ?
            <div className={css.error}>
                <h1>Oops, something happen. Please refresh...</h1>
                <button onClick={reloadPage}>RELOAD</button>
                <div className={css.orDivider}><span>Or</span></div>
                <VideoSearch contentStyle={css.searchInput} />
            </div>
            :
            <div className={css.notFound}>
                <div className={css.content}>
                    <img src="/assets/error.png" alt="Error"/>
                    <h1>404 Error</h1>
                    <span>Page not found</span>
                </div>
                <VideoSearch contentStyle={css.searchInput} />
            </div>
        }
    </div>
)