import React, { useState, useEffect } from "react";
import { youtube, youtubeParams } from "../../api/youtube";
import {Content, LoadingSpinner, Video, MessageEnd} from '../../components'
import ErrorPage from "../ErrorPage/ErrorPage";
import css from './HomePage.module.scss'

function HomePage() {
    const [err, setErr] = useState(false)
    const [popularVids, setPopularVids] = useState([])

    useEffect(() => {
        const getMostPopular = async () => {
            const popularData = await youtube.get('videos', {
                params: {
                    ...youtubeParams,
                    chart: 'mostPopular',
                    regionCode: 'us',
                    videoCategoryId: 1
                }
            })
            setPopularVids(popularData.data.items)
        }
        getMostPopular()
            .catch(err => {
                console.log(err.response.data.error)
                setErr(true)
            })
    }, [setPopularVids, setErr])

    const videos = popularVids.map((video) => {
        let vid = video.snippet
        return (
            <Video
                key={video.id}
                className={css.video}
                videoId={video.id}
                title={vid.title}
                videoInfoContainerCss={css.videoInfoContainer}
                thumbnailImg={vid.thumbnails.medium.url}
                channelTitle={vid.channelTitle}
            />
        )
    })

    if (err) return <ErrorPage not404={true} />
    return (
        <Content className={css.screen}>
            {popularVids.length !== 0
                ?
                <div className={css.content}>
                    <h1 className={css.pageTitle}>Most popular videos</h1>
                    <div className={css.videoMainList}>
                        { videos }
                    </div>
                    <MessageEnd message={`Max results (${popularVids.length})`} />
                </div>
                :
                <LoadingSpinner />
            }
        </Content>
    )
}

export default HomePage